<template>
  <div class="sidebar">
    <ul>
      <li
        v-for="(item, index) in items"
        :key="index"
        :class="{ active: selectedIndex === index }"
        @click="selectItem(index)"
      >
        {{ item }}
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "LeftSections",
  props: {
    sectionList: {
      type: Array,
      required: true,
    },
    chapter: {
      type: Number,
      required: true,
    },
    selectedChapter:{
      type: Number,
      required: true,
    },
    selectedSection:{
      type: Number,
      required: true,
    }
  },
  data() {
    return {
      items: [],
      selectedIndex: null,
    };
  },
  methods: {
    updateSelectedIndex() {
      // console.log('this.selectedChapter='+this.selectedChapter);
      // console.log('this.chapter='+this.chapter)
      if (this.selectedChapter !== this.chapter) {
      this.selectedIndex = null; // 如果 selectedChapter 不等于 chapter，则设置 selectedIndex 为 null
    }
    else {
      this.selectedIndex = this.selectedSection-1
    } 
    },
    selectItem(index) {
      this.selectedIndex = index;
      this.$emit('item-selected', this.chapter,index + 1) // 触发事件通知父组件
    },
  },
  created() {
    this.items = this.sectionList;
    this.selectedIndex = this.selectedChapter==this.chapter?this.selectedSection - 1:null
  },
  watch: {
    selectedChapter: 'updateSelectedIndex',
    selectedSection: 'updateSelectedIndex'
  },
};
</script>

<style scoped>
.sidebar {
  width: 200px;
  background-color: #ffffff;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  position: relative;
  
}



li.active::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background-color: #007bff;
  border-radius: 4px 0 0 4px;
  border: none
}

li.active {
  color: #007bff;
  background-color: transparent;
}
</style>