<template>
  <div class="home">
    <div class="container">
      <div class="left">
        <Left :sections="leftdata" :titles="titles" :selectedChapter="activeChapter" :selectedSection="activeSection" @item-set="setActive" />
      </div>
      <div class="html-content">
        <div v-html="htmlContent"></div>
        <div class="button-container">
          <button class="button left-button" v-if="!(activeChapter === 1 && activeSection === 1)" @click="prevSection">{{ prevButtonText }}</button>
          <button class="button right-button" @click="nextSection">{{ nextButtonText }}</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Left from '@/components/Left.vue'

export default {
  name: 'HomeView',
  components: {
    Left,
  },
  data() {
    return {
      leftdata: [
        [
          "回归分析模型的概述",
          "线性回归",
          "线性回归的概率解释",
          "线性回归的使用",
          "逻辑回归",
          "多项逻辑回归",
          "非线性回归与正则项",
          "本章小结"
        ],
        [
          "SVM优化算法—SMO",
          "特征映射与核方法",
          "常用核函数及其构造方法",
          "软间隔与松弛变量",
          "支持向量思想在回归问题上的应用—SVR模型",
          "支持向量机的发展历史和历史地位"
        ],
      ],
      titles: ["回归模型", "支持向量机SVM"],
      htmlContent: '', // 用于存放 HTML 文件内容
      activeChapter: 1,
      activeSection: 1,
    };
  },
  computed: {
    prevButtonText() {
      return this.activeSection > 1 ? '« 上一节' : '« 上一章';
    },
    nextButtonText() {
      const totalSections = this.leftdata[this.activeChapter - 1].length;
      return this.activeSection < totalSections ? '下一节 »' : '下一章 »';
    }
  },
  methods: {
    setActive(chapter, section) {
      console.log("chapter=" + chapter);
      console.log("section=" + section);
      var isChange = false;
      if (this.activeChapter != chapter || this.activeSection != section) {
        isChange = true;
      }
      this.activeChapter = chapter;
      this.activeSection = section;
      if (isChange) {
        this.loadHtmlContent();
      }
    },
    loadHtmlContent() {
      fetch(`https://21231114.github.io/data-mining/static/chapter_${this.activeChapter}_section_${this.activeSection}.html`)
        .then(response => response.text())
        .then(data => {
          this.htmlContent = data; // 加载 HTML 内容
        })
        .catch(error => console.error('Error loading HTML:', error));
    },
    prevSection() {
      // Logic to navigate to the previous section
      if (this.activeSection > 1) {
        this.activeSection--;
      } else if (this.activeChapter > 1) {
        this.activeChapter--;
        this.activeSection = this.leftdata[this.activeChapter - 1].length; // Go to the last section of the previous chapter
      }
      this.loadHtmlContent();
    },
    nextSection() {
      // Logic to navigate to the next section
      const totalSections = this.leftdata[this.activeChapter - 1].length;
      if (this.activeSection < totalSections) {
        this.activeSection++;
      } else if (this.activeChapter < this.leftdata.length) {
        this.activeChapter++;
        this.activeSection = 1; // Go to the first section of the next chapter
      }
      this.loadHtmlContent();
    }
  },
  mounted() {
    this.loadHtmlContent(); // 组件挂载时加载 HTML 内容
  }
}
</script>

<style>
.container {
  display: flex;
}

.html-content {
  padding: 20px;
  flex-grow: 1; /* 右侧内容自适应宽度 */
}

.button-container {
  display: flex;
  justify-content: flex-end; /* Align buttons to the right */
  margin-top: 20px; /* 可选，增加按钮与内容之间的间距 */
  padding: 0 50px; /* 控制按钮距离 */
}

/* Button styles */
.button {
  padding: 10px 20px;
  width: 150px;  /* 设置按钮的宽度 */
  height: 50px;  /* 设置按钮的高度 */
  border: 1px solid #ccc; /* 添加边框 */
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
  background-color: white; /* 按钮背景色 */
  color: #007bff; /* 按钮字体颜色 */
}

.left-button {
  margin-right: 20px; /* 左按钮与右按钮间距 */
}

.right-button {
  margin-left: 600px; /* 保持右按钮位置不变 */
  margin-right: 250px;
}

.button:hover {
  background-color: #f0f0f0; /* 悬停效果 */
  transform: translateY(-2px);
}

.button:active {
  transform: translateY(0);
}
</style>