<template>
    <div class="container">
      <div v-for="(section, index) in sections" :key="index" class="section">
        <h2
          :class="{ active: selectedChapter === index + 1 }"
          class="section-title"
          @click="setSelected(index+1,1)"
        >
          {{titles[index]}}
         
      </h2>
        <LeftSections :sectionList="section" :chapter="index + 1" :selectedChapter="selectedChapter" :selectedSection="selectedSection" @item-selected="setSelected" />
      </div>
    </div>
  </template>
  
  <script>
  import LeftSections from './LeftSections.vue';
  
  export default {
    name: "Left",
    components: {
      LeftSections,
    },
    props: {
      sections: {
        type: Array,
        required: true,
      },
      titles: {
        type: Array,
        required: true,
      },
      selectedChapter:{
        type: Number,
        required: true,
      },
      selectedSection:{
        type: Number,
        required: true,
      }
    },
    // data() {
    //   return {
    //     selectedChapter: null,
    //     selectedSection: null,
    //   };
    // },
    created() {
      this.selectedChapter = 1 //默认选择第一个
      this.selectedSection = 1
      // this.titles = titles
    },
    methods: {
      setSelected(val,section) {
        this.selectedChapter = val;
        this.selectedSection = section
        this.$emit('item-set',val,section)
        // console.log(" this.selectedChapter"+ this.selectedChapter);
        // console.log("this.selectedSection"+this.selectedSection);
      },
      // setChapter(chapter){
      //   setSelected(chapter,1);

      // },
    },
  };
  </script>
  
